* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
}
body {
  width: 100%;
  scroll-behavior: smooth;
  color: #fff;
}
a {
  text-decoration: none;
  color: #fff;
}
#navbar {
  background-color: rgb(165, 29, 58);
  position: fixed;
  width: 100%;
  top: 0;
  display: flex;
  justify-content: flex-end;
  gap: 20px;
  padding: 0 20px;
  z-index: 1;
}
#navbar a {
  font-size: x-large;

  padding: 25px;
}
#navbar a:hover {
  color: black;
}
#welcome-section {
  height: 70vh;
  width: 100%;
  background-color: rgb(48, 47, 47);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  font-size: 40px;
}

#welcome-section p {
  color: rgb(56, 157, 182);
  padding-top: 20px;
}

#projects {
  background-color: rgb(56, 157, 182);
  padding: 100px 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: black;
}
#projects h1 {
  font-size: 50px;

  padding-bottom: 10px;
  text-align: center;
  border-bottom: 3px solid black;
  margin-bottom: 30px;
}
.project_pics {
  display: flex;
   flex-wrap: wrap; 
  justify-content: center;
}
.pics {
  display: flex;
  flex-direction: column;
  width: 400px;
  
  height: 400px;
  position: relative;
  text-align: center;
 

  margin: 20px;
  transition: 0.5s;
}
.pics img {
  width: 100%;
  height: 100%;
  object-fit: fill;
}
.hidden_projects {
  opacity: 0;
}
#profile-link {
  color: #fff;
  background-color: rgb(48, 47, 47);
  font-size: 25px;
  display: flex;
  align-items: center;
  padding: 10px 20px;
  border: none;
}
.project-title {
  background-color: rgb(48, 47, 47);
  position: absolute;
  width: 100%;
  padding: 15px;

  bottom: 0;
  font-size: 20px;
}
.project_pics i {
  opacity: 0;
  padding: 0 10px;
  transition: 0.5s;
}
.pics:hover {
  transform: scale(1.1);

  box-shadow: 10px 10px 18px rgb(179, 13, 156);
}
.project_pics a:hover i {
  opacity: 1;
  color: rgb(180, 100, 26);
}
