footer {
  width: 100%;
  background-color: rgb(48, 47, 47);
  display: flex;

  align-items: center;
  height: a;
}
#footer_1 {
  width: 100%;

  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 100px 0;
  text-align: center;
}
#footer_1 h1 {
  margin-bottom: 30px;
  font-size: 60px;
}

.contacts {
  width: 100%;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
.contacts a {
  margin: 0 30px;
  font-size: x-large;
  position: relative;
  top: 0;
  transition: 0.5s;
  padding: 10px;
  color: rgb(56, 157, 182);
}
.contacts a i {
  margin-right: 10px;
}
.contacts a:hover {
  top: 20px;
}
@media (max-width: 1000px) {
  .pics img {
    width: 100%;
    height: 100%;
  }
}
@media (max-width: 500px) {
  .project_pics a {
    width: 100%;
  }
  #navbar {
    flex-direction: column;
    text-align: center;
  }
  #navbar a {
    padding: 10px;
  }
}
